import React from 'react'
import Script from 'react-load-script'
import headshot from '../img/content/supercropped.jpg'

export default function(props){
  return (<div>
      <div style={{display: 'flex', paddingTop: '72px'}}>
        <img src={headshot} />
    </div>
    <Script
      url={'https://actionnetwork.org/widgets/v3/form/join-caban-for-queens-full?format=js&source=widget'}
      onCreate={handleFormLoaded}
    />
  </div>)
}

function handleFormLoaded(){
  setTimeout(
    () => document.querySelector("#form-area").classList.add("loaded"),
    500
  );
}
